import React from "react";
import PropTypes from "prop-types";
import 'styles/keys-page/KeyCard.css';
import "styles/ToplButtons.css";
import {Unlock, Lock, More} from "grommet-icons";
import UnlockPopup from "./UnlockPopup";
import Popup from "../Popup";
import KeyDetails from "./KeyDetails";

/**
 * Props required:
 * publicAddress: string - address for this keycard,
 * keyFileInfo: {
        name: string,
        password: string,
        assets: array({boxType: string, balance: number}),
        unlocked: boolean
    },
 * currentUnlocked: string - public address of the currently unlocked key.
 * updateData: function - to update data (request data from backend)
 * lockKeyfile: function - to lock a keyfile
 */
const propTypes = {
    publicAddress: PropTypes.string.isRequired,
    keyFileInfo: PropTypes.shape({
        name: PropTypes.string,
        assets: PropTypes.arrayOf(PropTypes.shape({
            boxType: PropTypes.string,
            balance: PropTypes.number
        })),
        assetsList: PropTypes.arrayOf(PropTypes.shape({
            code: PropTypes.string,
            balance: PropTypes.number
        })),
        unlocked: PropTypes.bool
    }).isRequired,
    currentUnlocked: PropTypes.string,
    updateData: PropTypes.func.isRequired,
    lockKeyfile: PropTypes.func.isRequired
}

export default class KeyCard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showUnlockPopup: false,
            showKeyDetails: false
        }
        this.toggleUnlockPopup = this.toggleUnlockPopup.bind(this);
        this.showKeyDetails = this.showKeyDetails.bind(this);
    }

    toggleUnlockPopup() {
        this.setState({showUnlockPopup: !this.state.showUnlockPopup});
    }

    showKeyDetails() {
        this.setState({showKeyDetails: !this.state.showKeyDetails});
    }

    renderKeyDetails() {
        return (<Popup page={<KeyDetails address={this.props.publicAddress} name={this.props.keyFileInfo.name}
                                         lockKeyfile={this.props.lockKeyfile}
                                         unlocked={this.props.keyFileInfo.unlocked}/>}
                       toggle={this.showKeyDetails}/>)
    }

    renderBalances() {
        if (this.props.keyFileInfo.assets) {
            const polysBalance = this.props.keyFileInfo.assets.filter(asset => asset.boxType === "poly")
                .reduce(function (a, b) {
                    return a + b.balance
                }, 0);

            const arbitsBalance = this.props.keyFileInfo.assets.filter(asset => asset.boxType === "arbit")
                .reduce(function (a, b) {
                    return a + b.balance
                }, 0);

            return (
                <ul style={{listStyleType: "none"}}>
                    <li style={{paddingBottom: "10px"}} key={"poly-balance"}> {polysBalance} POLY</li>
                    <li style={{paddingBottom: "10px"}} key={"arbit-balance"}> {arbitsBalance} ARB</li>
                </ul>
            );
        }
    }

    render() {
        if (this.props.keyFileInfo.unlocked) {
            return (<div>
                <div className={"card-outline"} style={{backgroundColor: "#26A69A", color: "#FFFFFF"}}>
                    <h1 className={"wallet-header"}>
                        {this.props.keyFileInfo.name}
                        <Unlock size={"large"} color={"#FFFFFF"} style={{paddingLeft: "5px", float: "right"}}/>
                    </h1>
                    {this.renderBalances()}
                    <button className={"ellipse-button"} onClick={this.showKeyDetails}> <More color={"white"}/> </button>
                </div>
                    {this.state.showKeyDetails ? this.renderKeyDetails() : null}
            </div>);
        }else {
            return (<div>
                <div className={"card-outline"}>
                    <h1 className={"wallet-header"}>
                        {this.props.keyFileInfo.name}
                        <Lock size={"large"} color="black" style={{paddingLeft: "5px", float: "right"}}/>
                    </h1>
                    {this.renderBalances()}
                    <button className={"ellipse-button"} style={{backgroundColor: "white"}} onClick={this.showKeyDetails}> <More color={"#26A69A"}/> </button>
                    <button className={"round-blue-button"} style={{position: "relative", left: "20%", width: "60%",
                        fontSize: "18px", height: "40px", borderRadius: "30px"}} onClick={this.toggleUnlockPopup}>
                        Unlock
                    </button>
                </div>
                    {this.state.showKeyDetails ? this.renderKeyDetails() : null}
                    {this.state.showUnlockPopup
                        ? <UnlockPopup keyfileName={this.props.keyFileInfo.name} toggle={this.toggleUnlockPopup}
                               address={this.props.publicAddress} currentUnlocked={this.props.currentUnlocked}
                               updateData={this.props.updateData} lockKeyfile={this.props.lockKeyfile}/>
                               : null}
            </div>);
        }
    }
}

KeyCard.propTypes = propTypes