import React from 'react';
import PropTypes from 'prop-types';
import {FormDown, FormUp} from "grommet-icons";
import ReactTooltip from "react-tooltip";
import "styles/full-page/NetworkDropdown.css";
import {Checkmark} from "grommet-icons/es6";
import axios from "axios";

/**
 * Props (all required)
 *  currentNetwork: string - the currently selected network
 *  online: boolean - if connected to bifrost
 *  changeNetwork: function - function to switch networks
 *  showError: function - to show error
 */
const propTypes = {
    currentNetwork: PropTypes.string.isRequired,
    online: PropTypes.bool.isRequired,
    changeNetwork: PropTypes.func.isRequired,
    showError: PropTypes.func.isRequired
}

export class NetworkDropdown extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            networkDropdownOpen: false,
            listOfNetworks: new Map([["Toplnet", 1], ["Valhalla", 16],
                ["Private", 64], ["Local", 48]])
        }
        this.wrapperRef = React.createRef();
        this.toggleNetworkDropdown = this.toggleNetworkDropdown.bind(this);
        this.switchNetwork = this.switchNetwork.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
    }

    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    toggleNetworkDropdown() {
        this.setState({networkDropdownOpen: !this.state.networkDropdownOpen});
    }

    handleClickOutside(event) {
        if (this.wrapperRef && !this.wrapperRef.current.contains(event.target)) {
            this.setState({networkDropdownOpen: false});
        }
    }

    async switchNetwork(network) {
        if (!this.props.online) {
            const data = {
                jsonrpc: "2.0",
                id: "2",
                method: "wallet_changeNetwork",
                params: [{
                    "newNetwork": network.toLowerCase()
                }]
            }
            const headers = {"x-api-key": "test_key"}
            const response = await axios.post('http://localhost:9086/', data, {headers})
            if (response.data.error) {
                this.props.showError(response.data.error.message);
            }else {
                await this.props.changeNetwork(network);
            }
        }
    }

    renderDropdown() {
        let style = "dropdown-item"
        if (this.props.online) {
            style ="disabled-dropdown-item"
        }
        const networks = Array.from(this.state.listOfNetworks.keys());
        return(
            <ul className={"network-dropdown"}>
                <h1 className={"dropdown-title"}> Networks </h1>
                {networks.map(network => {
                    if (network === this.props.currentNetwork) {
                        return (<button className={style} key={network + "-button"}>
                                {network}
                                <Checkmark color="black" style={{float: "right", height: "17px"}}/>
                            </button>);
                    }else{
                        return (
                            <button className={style} data-tip="" data-for={"online-mode"}
                                        onClick={()=> this.switchNetwork(network)} key={network + "-button"}>
                                {network}
                            </button>
                        );
                    }
                })}
            </ul>
        );
    }

    render() {
        let buttonStyle, arrow;
        if (this.state.networkDropdownOpen) {
            buttonStyle = "network-button-selected";
            arrow = <FormUp color={"#FFFFFF"} style={{float: "right"}}/>;
        }else{
            buttonStyle = "network-button";
            arrow = <FormDown color="#26A69A"  style={{float: "right"}}/>;
        }
        return (
            <div ref={this.wrapperRef}>
                <button className={buttonStyle} onClick={this.toggleNetworkDropdown} data-tip="" data-for={"online-mode"}>
                    {this.props.currentNetwork}
                    {arrow}
                </button>
                {this.props.online
                    ? <ReactTooltip id={"online-mode"} place={"bottom"} effect={"solid"}>
                        Can't change network when Bifrost is running.
                    </ReactTooltip>
                    : null}
                {(this.state.networkDropdownOpen) ? this.renderDropdown() : null}
            </div>
        );
    }
}

NetworkDropdown.propTypes = propTypes