import React from 'react';
import {StatusGood} from "grommet-icons";
import PropTypes from "prop-types";
import CloseButton from "components/CloseButton";
import CopyButton from "components/CopyButton";
import "styles/Popup.css";

/**
 * Props required:
 *  toggle: function - to close the pop-up,
 *  txId: string - the transaction ID
 */
const propTypes = {
    toggle: PropTypes.func.isRequired,
    txId: PropTypes.string.isRequired
}

//Pop-up after successfully sending an online tx
export default class ConfirmationPopup extends React.Component {
    render() {
        return (
            <div className={"popup"} style={{padding: "15px 15px 0 15px"}}>
                <CloseButton handleClose={this.props.toggle} style={{marginTop: "5px", marginRight: "20px"}}/>
                <StatusGood size="large" color={"#04844B"} style={{float: "left", paddingLeft: "10px", paddingRight: "10px"}}/>
                <h1 style={{margin: "0", paddingBottom: "20px"}}> Transaction has been broadcasted! </h1>
                <span style={{display: "block", paddingBottom: "10px"}}> Transaction ID: </span>
                <span className={"tx-hash"} id={"tx-id"}>  {this.props.txId} </span>
                <div style={{display: "inline-block", verticalAlign: "middle"}}>
                    <CopyButton elementIDToCopy={"tx-id"}/>
                </div>
            </div>
        )
    }
}

ConfirmationPopup.propTypes = propTypes