import React from "react"
import 'styles/balances-page/WalletSummary.css';
import 'styles/ToplButtons.css';
import {Balance} from "./Balance";
import PropTypes from "prop-types";
import {AssetTable} from "./assets/AssetTable";
import {BiRefresh, ImSpinner3} from "react-icons/all";
import AddressBox from "./AddressBox";

/**
 * props:
 * public address (optional): string
 * array of assets (optional): (boxType, balance)
 * assetList (optional): {code: string, balance: number},
 * online (optional): boolean - if connected to bifrost or not
 * retrievingData (required): boolean - if still retrieving data = true, else false
 * allLocked (optional): boolean - if all of the keys are locked
 * openKeys (optional): function - function to open keys page.
 * updateData (required) - function - function that retrieves data from back-end
 */
const propTypes = {
    publicAddress: PropTypes.string,
    assets: PropTypes.arrayOf(PropTypes.shape({
        boxType: PropTypes.string,
        balance: PropTypes.number
    })),
    assetsList: PropTypes.arrayOf(PropTypes.shape({
        code: PropTypes.string,
        balance: PropTypes.number
    })),
    online: PropTypes.bool.isRequired,
    retrievingData: PropTypes.bool.isRequired,
    allLocked: PropTypes.bool,
    openKeys: PropTypes.func,
    updateData: PropTypes.func.isRequired
}

export class WalletSummary extends React.Component{

    constructor(props) {
        super(props);
        this.state={
            showMsg: false
        }
        this.showRecentTxMsg = this.showRecentTxMsg.bind(this);
        this.refresh = this.refresh.bind(this);
    }


    renderBalanceList() {
        return this.props.assets.map(asset =>
            <Balance publicAddress={this.props.publicAddress} balance={asset.balance} boxType={asset.boxType}
                     online={this.props.online} showRecentTxMsg={this.showRecentTxMsg}/>
        )
    }

    showRecentTxMsg() {
        this.setState({showMsg: !this.state.showMsg});
    }

    refresh() {
        if (this.state.showMsg) {
            this.setState({showMsg: false})
        }
        this.props.updateData();
    }


    render() {
        let msg;
        if (this.props.retrievingData) {
            msg = <div>
                <ImSpinner3 style={{animation: "rotation 2s infinite linear"}}/>
                Loading your keyfiles...
            </div>
        }else if (this.props.allLocked) {
            msg = <div>
                All of your keys are currently locked. You can unlock a key on the
                <button className={"text-button"} onClick={this.props.openKeys}> keys page </button>.
            </div>

        } else if (this.props.publicAddress != null) {
            let innerMsg;
            if (!this.props.online) {
                innerMsg = "You're not connected to bifrost, so you're balances may not be up to date."
            }
            msg = <div style={{color: "red"}}>
                {innerMsg}
                {this.props.assets
                    ? null
                    : <div> <Balance publicAddress={this.props.publicAddress} balance={0} boxType={"poly"}
                         online={this.props.online} showRecentTxMsg={this.showRecentTxMsg}/>
                         <Balance publicAddress={this.props.publicAddress} balance={0} boxType={"arbit"}
                                  online={this.props.online} showRecentTxMsg={this.showRecentTxMsg}/> </div>}
            </div>
        }else{
            msg = <div>
                You currently do not have any keys. You can import or generate a new key on the
                <button className={"text-button"} onClick={this.props.openKeys}> keys page </button>.
            </div>
        }
        return (
                <div className={"outer-gray"}>
                    <div className={"inner-box"}>
                        <AddressBox publicAddress={this.props.publicAddress}/>
                    </div>

                    <div className={"inner-box"}>
                        <div className={"table-title"}>
                            Balances
                            <button className={"refresh-button"} onClick={this.refresh}>
                                <BiRefresh className={"refresh-symbol"}/>
                            </button>
                            {this.state.showMsg ?
                            <span style={{fontSize: "15px", color: "red"}}> Recently sent transactions. Balances may be out of date. </span>
                            : null}
                        </div>
                        {this.renderBalanceList()}
                        {msg}
                    </div>

                    <div className={"inner-box"}>
                        <div className={"table-title"}>
                            Other Assets
                            <button className={"refresh-button"} onClick={this.refresh}>
                                <BiRefresh className={"refresh-symbol"}/>
                            </button>
                            {this.state.showMsg ?
                                <span style={{fontSize: "15px", color: "red"}}> Recently sent transactions. Assets information may be out of date. </span>
                                : null}
                        </div>
                        <AssetTable assetsList={this.props.assetsList} currentAddress={this.props.publicAddress}
                                    online={this.props.online} retrievingData={this.props.retrievingData}
                                    showRecentTxMsg={this.showRecentTxMsg}/>
                    </div>
                </div>
            )
    }
}

WalletSummary.propTypes = propTypes


