import React from 'react';
import PropTypes from "prop-types";
import {BiPencil, BiTrash} from "react-icons/all";
import "styles/settings-page/SettingsPage.css";
import axios from "axios";
import {Checkmark} from "grommet-icons";
import ErrorPopup from "../ErrorPopup";
import Popup from "../Popup";
import ConfirmRemovePopup from "./ConfirmRemovePopup";

/**
 * Props required:
 *     name: string - name for a chain provider
 *     chainProvider: string
 *     edit: function for editing a chain provider or its name
 *     connected: function to update WalletApp state when connecting to new chainProvider
 *     currentlyConnected: boolean
 *     disconnectFn: function to disconnect from chain provider
 *     remove: function to remove chain provider from list
 *  Optional:
 *     editMode: boolean - tells whether it is in edit mode or not
 */
const propTypes = {
    name: PropTypes.string.isRequired,
    chainProvider: PropTypes.string.isRequired,
    edit: PropTypes.func.isRequired,
    connected: PropTypes.func.isRequired,
    currentlyConnected: PropTypes.bool.isRequired,
    disconnectFn: PropTypes.func.isRequired,
    remove: PropTypes.func.isRequired,
    editMode: PropTypes.bool
}

/**
 * The row to display a chain provider in the table of chain providers to connect to
 */
export default class NetworkRow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            editMode: this.props.editMode,
            networkName: this.props.name,
            chainProvider: this.props.chainProvider,
            error: false,
            errorMsg: "",
            showRemovePopup: false
        }
        this.toggleEdit = this.toggleEdit.bind(this);
        this.toggleRemovePopup = this.toggleRemovePopup.bind(this);
        this.saveInfo = this.saveInfo.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.connectToChainProvider = this.connectToChainProvider.bind(this);
        this.disconnect = this.disconnect.bind(this);
        this.closeError = this.closeError.bind(this);
    }

    toggleEdit() {
        this.setState({editMode: !this.state.editMode});
    }
    toggleRemovePopup() {
        this.setState({showRemovePopup: !this.state.showRemovePopup});
    }

    closeError() {
        this.setState({error: false});
    }

    saveInfo() {
        this.props.edit({name: this.state.networkName, chainProvider: this.state.chainProvider});
        this.toggleEdit();
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    async connectToChainProvider() {
        const connectRequest = {
            jsonrpc: "2.0",
            id: "2",
            method: "onlineWallet_connectToBifrost",
            params: [{
                "chainProvider": this.state.chainProvider
            }]
        }
        const headers = {"x-api-key": "test_key"}
        const response = await axios.post('http://localhost:9086/', connectRequest, {headers});
        if (response.data.error) {
            this.setState({
                error: true,
                errorMsg: response.data.error
            });
        }else{
            this.props.connected(this.state.chainProvider);
        }

    }

    async disconnect() {
        const disconnectRequest = {
            jsonrpc: "2.0",
            id: "2",
            method: "onlineWallet_disconnectFromBifrost",
            params: [{}]
        }
        const headers = {"x-api-key": "test_key"}
        await axios.post('http://localhost:9086/', disconnectRequest, {headers});
        this.props.disconnectFn();
    }

    render() {
        if (this.state.editMode) {
            return (
                <tr>
                    <td className={"asset-cell"}>
                        <input type={"text"} name={"networkName"} value={this.state.networkName}
                               className={"edit-info"} onChange={this.handleChange}/>
                    </td>
                    <td className={"asset-cell"} style={{width: "300px"}}>
                        <input type={"text"} name={"chainProvider"} value={this.state.chainProvider}
                               className={"edit-info"} style={{width: "300px"}} onChange={this.handleChange}/>
                    </td>
                    <td>
                        <button className={"text-button"} style={{color: "black"}} onClick={this.saveInfo}>
                            Done
                        </button>
                    </td>
                </tr>
            )
        }else {
            let button =
                <button className={"blue-square-button"} style={{float: "none", width: "80px", height: "25px",
                    fontSize: "14px", margin: "inherit", border: "none", marginLeft: "15%"}} onClick={this.connectToChainProvider}>
                    Connect
                </button>

            if (this.props.currentlyConnected) {
                button = <button className={"import-button"} style={{fontSize: "14px", border: "none", fontFamily: "Roboto", outline: "none"}}
                onClick={this.disconnect}>
                    <span> Disconnect </span>
                </button>
            }

            return (
                <tr>
                    <td className={"asset-cell"}>
                        {this.props.currentlyConnected
                            ? <Checkmark style={{float: "left", height: "15px", stroke: "#26A69A"}}/>
                            : null
                        }
                        {this.props.name}
                    </td>
                    <td className={"asset-cell"} style={{width: "300px"}}> {this.props.chainProvider}</td>
                    <td>
                        <button className={"text-button"} style={{color: "black"}} onClick={this.toggleEdit}>
                            <BiPencil/>
                        </button>
                    </td>
                    <td>
                        <button className={"text-button"} style={{color: "black", marginLeft: "35%"}}
                                onClick={this.toggleRemovePopup}>
                            <BiTrash/>
                        </button>
                    </td>
                    <td>
                        {button}
                    </td>
                    {this.state.error
                        ? <Popup page={<ErrorPopup message={this.state.errorMsg}/>} toggle={this.closeError}/>
                        : null}
                    {this.state.showRemovePopup
                        ? <Popup page={<ConfirmRemovePopup removeFn={this.props.remove} toggle={this.toggleRemovePopup}/>}
                                 toggle={this.toggleRemovePopup} height={"200px"}/>
                        : null}
                </tr>
            )
        }
    }
}

NetworkRow.propTypes = propTypes