import React from "react";
import CloseButton from "../CloseButton";
import PropTypes from "prop-types";
import "styles/ToplButtons.css";
import axios from "axios";
import {ImSpinner3} from "react-icons/all";

/**
 * Props required:
 *     toggle: function - for opening/closing popup
 *     language: string - language code used for seed phrase language when importing key file
 *     lockCurrent: function - to lock current keyfile when importing new keyfile.
 */
const propTypes = {
    toggle: PropTypes.func.isRequired,
    language: PropTypes.string.isRequired,
    lockCurrent: PropTypes.func.isRequired
}

export default class ImportKeyfilePopup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            mnemonicPhrase: "",
            password: "",
            errorMsg: null,
            loading: false
        }
        this.handleChange = this.handleChange.bind(this);
        this.addKeyfile = this.addKeyfile.bind(this);
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    parseBalancesResponse(response, pubKey) {
        const json = JSON.parse(response)
        const polys = json[pubKey]["Balances"]["Polys"]
        const arbits = json[pubKey]["Balances"]["Arbits"]
        return [{boxType: "poly", balance: polys},
            {boxType: "arbit", balance: arbits}]
    }

    async addKeyfile() {
        this.setState({loading: true});
        const data = {
            jsonrpc: "2.0",
            id: "2",
            method: "wallet_importKeyfile",
            params: [{
                "password": this.state.password,
                "seedPhrase": this.state.mnemonicPhrase,
                "seedPhraseLang": this.props.language
            }]
        }
        const headers = {"x-api-key": "test_key"}
        const response = await axios.post('http://localhost:9086/', data, {headers})
        if (response.data.error) {
            this.setState({errorMsg: response.data.error.message, loading: false});
        }else{
            await this.props.lockCurrent();
            this.setState({loading: false});
            this.props.toggle();
        }
    }


    render() {
        return (
            <div className={"new-keyfile-popup"} style={{height: "480px"}}>
                <CloseButton handleClose={this.props.toggle}/>
                <h1 style={{marginTop: "5px"}}> Import keyfile </h1>
                {this.state.loading
                    ? <div style={{fontWeight: 400, fontSize: "14px", paddingTop: "15px"}}>
                        <ImSpinner3 style={{animation: "rotation 2s infinite linear", marginRight: "5px"}}/>
                        Importing key file...
                    </div>
                    :<div>
                        <div className={"name-field"} style={{paddingBottom: "20px"}}>
                            <span> Keyfile name: </span>
                            <input className={"input-style"} style={{width: "90%"}} type={"text"} name={"name"}
                               placeholder={"Enter keyfile name"} value={this.state.name} onChange={this.handleChange}/>
                        </div>
                        <div className={"name-field"} style={{paddingBottom: "20px"}}>
                            <span> Mnemonic phrase: </span>
                            <textarea className={"input-style"} style={{width: "90%", height: "90px", fontFamily: "Roboto", padding: "5px"}} name={"mnemonicPhrase"}
                                      placeholder={"Enter mnemonic phrase"} value={this.state.mnemonicPhrase} onChange={this.handleChange}/>
                        </div>
                        <div className={"password"} style={{float: "none"}}>
                            <span> Enter password: </span>
                            <input className={"input-style"} style={{width: "100%"}} type={"password"} name={"password"}
                                   placeholder={"Password"} value={this.state.password} onChange={this.handleChange}/>
                        </div>
                        {this.state.errorMsg ? <div style={{color: "red"}}> {this.state.errorMsg}</div> : null}
                        <button className={"blue-square-button"} style={{float: "right"}} onClick={async () => { await this.addKeyfile()}}>
                            Import
                        </button>
                    </div>
                }
            </div>
        );
    }
}

ImportKeyfilePopup.propTypes = propTypes