import React from "react";
import PropTypes from "prop-types";
import "styles/Popup.css";
import CloseButton from "./CloseButton";

/**
 * Props required:
 *  page: element - the dom.element to be rendered within the pop-up,
 *  toggle: function - to close the pop-up
 * Optional:
 *  width: string - width of pop-up
 *  height: string - height of pop-up
 */
const propTypes = {
    page: PropTypes.element.isRequired,
    toggle: PropTypes.func.isRequired,
    width: PropTypes.string,
    height: PropTypes.string
}

export default class Popup extends React.Component {

    render() {
        return (
            <div className={"popup"} style={{width: this.props.width, height: this.props.height}}>
                <CloseButton style={{marginRight: "20px"}} handleClose={this.props.toggle} />
                {this.props.page}
            </div>
        )
    }
}

Popup.propTypes = propTypes