import React from 'react';
import PropTypes from "prop-types";
import {FormAdd} from "grommet-icons";
import CreateAssetPopup from "./CreateAssetPopup";
import AssetRow from "./AssetRow";
import "styles/balances-page/assets/AssetTable.css"
import "styles/ToplButtons.css";

/**
 * Props:
 *  Required:
 *      retrievingData: boolean - if still waiting on data from backend
 *      showRecentTxMsg: function - for showing message after a recent tx
 *  Optional:
 *      assetsList: [{code, balance}] - list of assets
 *      currentAddress: string - address of currently unlocked key file
 *      online: boolean - if connected to bifrost or not
 */
const propTypes = {
    retrievingData: PropTypes.bool.isRequired,
    showRecentTxMsg: PropTypes.func.isRequired,
    assetsList: PropTypes.arrayOf(PropTypes.shape({
        code: PropTypes.string,
        balance: PropTypes.number,
    })),
    currentAddress: PropTypes.string,
    online: PropTypes.bool
}

export class AssetTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showAssetPopup: false,
            sendOrReceiveOpen: false
        }
        this.togglePopup = this.togglePopup.bind(this);
    }

    renderTable() {
        return this.props.assetsList.map((asset) => {
            const{code, balance} = asset;
            return <AssetRow balance={balance} code={code} currentAddress={this.props.currentAddress}
                             online={this.props.online}  showRecentTxMsg={this.props.showRecentTxMsg}/>
        })
    }

    capitalize (string) {
        if (typeof string !== 'string') return '';
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    renderTableHeader() {
        let header = Object.keys(this.props.assetsList[0]);
        return header.map((key) => {
                return <th className={"table-header"}>{this.capitalize(key)}</th>
        })
    }

    togglePopup() {
        this.setState({showAssetPopup: !this.state.showAssetPopup});
    }

    render() {
        if (this.props.assetsList.length > 0) {
            return (
                <div style={{overflow: "auto"}}>
                    <table className={"other-assets"}>
                        <thead>
                            <tr>
                                {this.renderTableHeader()}
                                <th className={"table-header-actions"}> Actions </th>
                            </tr>
                        </thead>
                        <tbody>{this.renderTable()}</tbody>
                    </table>
                    {this.state.showAssetPopup
                        ? <CreateAssetPopup  currentAddress={this.props.currentAddress} toggle={this.togglePopup}
                                             showRecentTxMsg={this.props.showRecentTxMsg}/>
                        : <button className={"round-blue-button"} style={{left: "36%", width: "156px", position: "relative", marginTop: "20px"}} onClick={this.togglePopup}>
                            <FormAdd color={"white"} thickness={"large"} style={{height: "25px", verticalAlign: "middle"}}/>
                            Create Asset
                        </button>}
                </div>
            )
        }else{
            let msg;
            if (this.props.retrievingData) {
                msg = <span> Loading assets... </span>
            }else if (!this.props.online) {
                msg = <span style={{color: "red", paddingLeft: "25%"}}>
                    You're not connected to bifrost, so you're assets cannot be retrieved.
                </span>
            } else if (this.props.currentAddress) {
                msg =
                    <div>
                        <span style={{paddingLeft: "30%"}}> You currently do not have any assets. </span>
                        <div>
                            {this.state.showAssetPopup
                                ? <CreateAssetPopup currentAddress={this.props.currentAddress} toggle={this.togglePopup}
                                                    showRecentTxMsg={this.props.showRecentTxMsg}/>
                                : <button className={"round-blue-button"} style={{left: "36%", width: "156px", position: "relative", marginTop: "20px"}} onClick={this.togglePopup}>
                                    <FormAdd color={"white"} thickness={"large"} style={{height: "25px", verticalAlign: "middle"}}/>
                                    Create Asset
                                </button>}
                        </div>
                    </div>
            }
            return (
                <div style={{overflow: "auto", fontSize: "14px"}}>
                    <table className={"other-assets"}>
                        <thead>
                            <tr>
                                <th className={"table-header"}> Code </th>
                                <th className={"table-header"}> Balance </th>
                                <th className={"table-header"}> Actions </th>
                            </tr>
                        </thead>
                    </table>
                    {msg}
                </div>
            )
        }

    }
}

AssetTable.propTypes = propTypes