import React from 'react';
import PropTypes from "prop-types";
import {Send} from "grommet-icons";
import SendPopup from "../send/SendPopup";
import "styles/balances-page/assets/AssetRow.css";

/**
 * Props (all required):
 *  balance: number - the balance for this asset
 *  code: string - the assetCode for this asset
 *  currentAddress: string - the address of the currently unlocked keyfile
 *  online: boolean - connected to bifrost or not.
 *  showRecentTxMsg: function - to show message that a recent transaction has occurred
 */
const propTypes = {
    balance: PropTypes.number.isRequired,
    code: PropTypes.string.isRequired,
    currentAddress: PropTypes.string.isRequired,
    online: PropTypes.bool.isRequired,
    showRecentTxMsg: PropTypes.func.isRequired
}

export default class AssetRow extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            sendOpen: false,
        }
        this.toggleSend = this.toggleSend.bind(this);
    }

    toggleSend() {
        this.setState({
            sendOpen: !this.state.sendOpen,
        });
    }

    renderSendButton() {
        return(
            <div className={"asset-table-action-buttons"}>
                <button className={"asset-table-action-button"} onClick={this.toggleSend}>
                    <span> Send </span>
                    <Send size="small" style={{verticalAlign: "middle"}}/>
                </button>
            </div>
        );
    }

    render() {
        return (
            <tr>
                <td className={"asset-cell"}>{this.props.code}</td>
                <td className={"asset-cell"}>{this.props.balance}</td>
                <td> {this.renderSendButton()}</td>
                {this.state.sendOpen
                    ? <div className={"send-popup"}>
                        <h1 style={{fontFamily: "Roboto", fontSize: "18px", paddingLeft: "44px"}}>
                            {this.props.code}: {this.props.balance}
                        </h1>
                        <SendPopup boxType={"asset"} fromAddress={this.props.currentAddress}
                                   toggleSend={this.toggleSend}  online={this.props.online}
                                   currentBalance={this.props.balance} showRecentTxMsg={this.props.showRecentTxMsg}/>
                    </div>
                    : null}
            </tr>
        )

    }
}

AssetRow.propTypes = propTypes