import React from 'react';
import PropTypes from "prop-types";
import "styles/balances-page/send/TxDataTable.css";
import CloseButton from "components/CloseButton";

/**
 * Props:
 *  data: object - the data for a newBox in a transaction (as a json object)
 */
const propTypes = {
    data: PropTypes.object.isRequired
}

export default class NewBox extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            dropdown: false
        }
        this.toggleDropdown = this.toggleDropdown.bind(this);
    }

    toggleDropdown() {
        this.setState({dropdown: !this.state.dropdown});
    }

    renderDetails() {
        const keys = Object.keys(this.props.data);
        return <table style={{paddingLeft: "10px", marginBottom: "10px"}}>
            <tbody>
            {keys.map((key) => {
                if (key !== "id") {
                    return <tr>
                        <td>{key}:</td>
                        <td> {JSON.stringify(this.props.data[key])} </td>
                    </tr>
                }else{
                    return null
                }
            })}
            </tbody>
        </table>
    }

    render() {
        let button = <span style={{underline: "solid", textDecoration: "underline"}}> Details </span>
        if (this.state.dropdown) {
            button = <CloseButton handleClose={this.toggleDropdown}/>
        }
        return (
            <div>
                {this.props.data["id"]}
                <button onClick={this.toggleDropdown} className={"box-drop"}>
                    {button}
                </button>
                {this.state.dropdown ? this.renderDetails() : null}
            </div>
        )
    }
}

NewBox.propTypes = propTypes