import React from "react";
import PropTypes from "prop-types";
import "styles/balances-page/send/SendOffline.css";
import "styles/balances-page/send/SendPopup.css";
import "styles/ToplButtons.css";
import GenerateRawTx from "./offline/GenerateRawTx";
import ImportRawTx from "./offline/ImportRawTx";
import ConfirmSendTx from "./offline/ConfirmSendTx";
import Popup from "components/Popup";
import UnsuccessfulTx from "./UnsuccessfulTx";
import CopyButton from "components/CopyButton";
import ImportSignedTx from "./offline/ImportSignedTx";

/**
 * Props (all required):
 *  online: boolean - whether gjallarhorn's connected to bifrost or not.
 *  boxType: string - "arbit", "poly", or "asset"
 *  toggleSendOffline: function - toggles between send offline and online
 *  fromAddress: string - the address of the currently unlocked keyfile
 *  recipients: [{address, amount}] - list of recipients and amount to send.
 *  changeAddress: string
 *  consolidationAddress: string
 *  postBroadcast: function - used to show confirmation po-up with tx id.
 *  toggleSend: function - toggles the send tab
 *  showError: function - to show input error in the SendPopup
 *  showConfirmation: function to show a pop-up confirming the transaction.
 */
const propTypes = {
    online: PropTypes.bool.isRequired,
    boxType: PropTypes.string.isRequired,
    toggleSendOffline: PropTypes.func.isRequired,
    fromAddress: PropTypes.string.isRequired,
    recipients: PropTypes.arrayOf(PropTypes.shape({
        address: PropTypes.string,
        amount: PropTypes.number
    })).isRequired,
    changeAddress: PropTypes.string.isRequired,
    consolidationAddress: PropTypes.string.isRequired,
    postBroadcast: PropTypes.func.isRequired,
    toggleSend: PropTypes.func.isRequired,
    showError: PropTypes.func.isRequired
}

/**
 * Offline process by "txStep":
 *  1. generateTx (online) - createRawTx,
 *  2. importRawTx (offline) - choice: sign/export, generateSig, addSig
 *  3. exportSignedTx or exportSignature or addSig (offline) ,
 *  4. importSignedTx (online),
 *  5. sendTx (online) - confirm transaction before broadcasting,
 *  6. successfulTx or unsuccessfulTx (online)
 */
export default class SendOffline extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            txStep: this.props.online ? "generateTx" : "initialpage",
            signedTx: "",
            txId: null,
            signedTxError: false,
            generateTxError: false,
            showUnsuccessfulPopup: false,
            errorMsg: ""
        }
        this.handleChange = this.handleChange.bind(this);
        this.changeStep = this.changeStep.bind(this);
        this.unsuccessfulTx = this.unsuccessfulTx.bind(this);
        this.successfulTx = this.successfulTx.bind(this);
        this.saveSignedTx = this.saveSignedTx.bind(this);
        this.saveImportedSignedTx = this.saveImportedSignedTx.bind(this);
    }

    changeStep(newStep) {
        this.setState({txStep: newStep});
    }

    //after signing raw tx
    saveSignedTx(data, step) {
        if (step === "addSig") {
            this.setState({
                signedTx: data,
                txId: data["txId"]
            });
        }else {
            if (typeof data.error === "object") {
                this.setState({
                    signedTxError: true,
                    errorMsg: JSON.stringify(data.error)
                })
            } else {
                this.setState({
                    signedTx: data.result["tx"],
                    txId: data.result["tx"]["txId"]
                })
            }
        }
        this.changeStep(step);
    }

    saveImportedSignedTx(data) {
        this.setState({signedTx: data})
        this.changeStep("sendTx")
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    unsuccessfulTx(msg) {
        this.changeStep("unsuccessfulTx");
        this.setState({errorMsg: msg});
        if (this.state.showUnsuccessfulPopup) {
            this.props.toggleSend()
        }
        this.setState({
            showUnsuccessfulPopup: !this.state.showUnsuccessfulPopup
        })
    }

    successfulTx(result) {
        this.changeStep("successfulTx");
        this.props.postBroadcast(result["txId"]);
    }


    render() {
        let render;
        //initialpage:
        render = <ImportRawTx saveSignedTx={this.saveSignedTx} fromAddress={this.props.fromAddress} changeStep={this.changeStep}/>

        if (this.state.txStep === "generateTx") {
            render = <GenerateRawTx toggleSendOffline={this.props.toggleSendOffline} alreadyHaveSignedTx={this.alreadyHaveSignedTx}
                                    fromAddress={this.props.fromAddress}
                                    changeAddress={this.props.changeAddress} consolidationAddress={this.props.consolidationAddress}
                                    recipients={this.props.recipients} boxType={this.props.boxType} showError={this.props.showError}
                                    changeStep={this.changeStep} online={this.props.online}/>
        }
        else if (this.state.txStep === "exportSignedTx" || this.state.txStep === "exportSignature" || this.state.txStep === "addSig") {
            let title, body, data;
            //Export signed tx:
            if (this.state.txStep === "exportSignedTx" || this.state.txStep === "addSig") {
                data = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.state.signedTx));
                title = "Signed Transaction ID";
                body =
                    <div>
                        <div className={"sign-tx-gray-box"} id={"signedTransaction"}>
                            {this.state.txId}
                        </div>
                        <div style={{float: "left", marginRight: "10px"}}> <CopyButton elementIDToCopy={"signedTransaction"}/> </div>
                        <button className={"blue-square-button"} style={{float: "none", fontSize: "16px"}}>
                            <a href={data} download={"topl-signed-tx.json"} style={{color: "white"}}> Download JSON </a>
                        </button>
                    </div>

            //Export signatures:
            }else if (this.state.txStep === "exportSignature") {
                data = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.state.signedTx["signatures"]));
                title = "Signature(s)";
                body =
                    <div style={{display: "inline-block", margin: "10px"}}>
                        <button className={"blue-square-button"} style={{float: "none", fontSize: "16px"}}>
                            <a href={data} download={"topl-signatures.json"} style={{color: "white"}}> Download JSON </a>
                        </button>
                    </div>
            }
         render = (
             <div>
                 <h1 style={{paddingLeft: "10px", fontSize: "20px", display: "inline"}}> {title} </h1>
                 {this.state.signedTxError
                     ? <span style={{color: "red", margin: "10px", padding: "10px"}}>{this.state.errorMsg}</span>
                     : body }

             </div>
         )

        } else if (this.state.txStep === "importSignedTx") {
            render = <ImportSignedTx saveData={this.saveImportedSignedTx} changeStep={this.changeStep}/>
        } else if (this.state.txStep === "sendTx") {
            render = <ConfirmSendTx changeStep={this.changeStep} signedTx={this.state.signedTx} unsuccessfulTx={this.unsuccessfulTx} successfulTx={this.successfulTx}/>
        }

        return <div> {render}
            {this.state.showUnsuccessfulPopup
                ? <Popup page={<UnsuccessfulTx signedTx={JSON.stringify(this.state.signedTx)} errorMsg={this.state.errorMsg}/>} toggle={this.unsuccessfulTx}/>
                : null
            }
        </div>
    }
}

SendOffline.propTypes = propTypes