import React from 'react';
import PropTypes from "prop-types";
import NetworkRow from "./NetworkRow";

/**
 * Props (all required:
 *     chainProviders: array of {name: string,chainProvider: string}
 *     edit: function to edit a chain provider or its name
 *     remove: function to remove chain provider from list
 *     connected: function to update WalletApp state when connecting to new chainProvider
 *     currentChainProvider: string
 *     disconnectFn: function to disconnect from chain provider
 *     online: boolean - if gjallarhorn is connected to bifrost or not.
 */
const propTypes = {
    chainProviders: PropTypes.arrayOf(PropTypes.shape({
        name: PropTypes.string,
        chainProvider: PropTypes.string
    })).isRequired,
    edit: PropTypes.func.isRequired,
    remove: PropTypes.func.isRequired,
    connected: PropTypes.func.isRequired,
    currentChainProvider: PropTypes.string.isRequired,
    disconnectFn: PropTypes.func.isRequired,
    online: PropTypes.bool.isRequired
}

/**
 * To manage chain providers to connect to
 */
export class NetworkTable extends React.Component {

    renderHeaders() {
        return (<tr>
            <th className={"table-header"} style={{fontSize: "14px"}}> Name </th>
            <th className={"table-header"} style={{fontSize: "14px"}}> Chain Provider </th>
            <th className={"table-header"} style={{fontSize: "14px"}}> Edit </th>
            <th className={"table-header"} style={{fontSize: "14px"}}> Remove </th>
            <th className={"table-header"}/>
        </tr>)
    }

    render() {
        return (
            <table style={{width: "90%", minWidth: "700px"}}>
                <thead>
                {this.renderHeaders()}
                </thead>
                <tbody>
                {this.props.chainProviders.map((info,index) => {
                    if (info.name === "" && info.chainProvider === "") {
                        return <NetworkRow name={info.name} chainProvider={info.chainProvider} edit={(newInfo) => this.props.edit(index, newInfo)}
                                           editMode={true} connected={this.props.connected}
                                           currentlyConnected={(info.chainProvider === this.props.currentChainProvider && this.props.online)}
                                           disconnectFn={this.props.disconnectFn} remove={()=>this.props.remove(index)}/>
                    } else {
                        return <NetworkRow name={info.name} chainProvider={info.chainProvider} edit={(newInfo) => this.props.edit(index, newInfo)}
                                           connected={this.props.connected} currentlyConnected={(info.chainProvider === this.props.currentChainProvider && this.props.online)}
                                           disconnectFn={this.props.disconnectFn} remove={()=>this.props.remove(index)}/>
                    }
                })}
                </tbody>
            </table>
        )
    }
}

NetworkTable.propTypes = propTypes