import React from "react";
import PropTypes from 'prop-types';
import {Checkmark, FormDown, FormUp} from "grommet-icons";
import "styles/settings-page/LanguageDropdown.css";

/**
 *  Props (Required):
 *     currentLanguageCode: the code for the currently selected language,
 *     switchLanguage: function - for switching the selected language
 */
const propTypes = {
    currentLanguageCode: PropTypes.string.isRequired,
    switchLanguage: PropTypes.func.isRequired
}

/**
 * The language used for the world list when generating mnemonic phrase for new key
 */
export default class LanguageDropdown extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            languageOptions: new Map ([["Chinese (Simplified)", "zh-hans"], ["Chinese (Traditional)","zh-hant"],
                ["English","en"], ["French","fr"], ["Italian","it"], ["Japanese", "ja"], ["Korean", "ko"], ["Spanish", "es"]])
        }
        this.wrapperRef = React.createRef();
        this.toggleDropdown = this.toggleDropdown.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
    }

    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    handleClickOutside(event) {
        if (this.wrapperRef && !this.wrapperRef.current.contains(event.target)) {
            this.setState({open: false});
        }
    }

    toggleDropdown() {
        this.setState({open: !this.state.open});
    }

    renderDropdown() {
        const languages = Array.from(this.state.languageOptions.keys());
        return (
            <ul className={"language-dropdown"}>
                <h1 className={"dropdown-title"}> Languages </h1>
                {languages.map(lang => {
                    if (this.state.languageOptions.get(lang) === this.props.currentLanguageCode) {
                        return (
                            <button className={"language-dropdown-item"} key={lang + "-button"}>
                                {lang}
                                <Checkmark color={"black"} style={{float: "right", height: "17px"}}/>
                            </button>
                        )
                    } else {
                        return (
                            <button className={"language-dropdown-item"} key={lang+"-button"}
                                    onClick={() => this.props.switchLanguage(this.state.languageOptions.get(lang))}>
                                {lang}
                            </button>
                        )
                    }
                })}
            </ul>
        )
    }

    getLanguageFromCode(code) {
        const languages = Array.from(this.state.languageOptions.keys());
        return languages.filter(lang => this.state.languageOptions.get(lang) === code);
    }


    render() {
        let arrow;
        if (this.state.open) {
            arrow = <FormUp color={"#171717"} style={{float: "right"}}/>;
        }else{
            arrow = <FormDown color={"#171717"} style={{float: "right"}}/>;
        }
        return (
            <div ref={this.wrapperRef} style={{display: "inline", position: "relative"}}>
                <button className={"language-button"} onClick={this.toggleDropdown}>
                    {this.getLanguageFromCode(this.props.currentLanguageCode)}
                    {arrow}
                </button>
                {this.state.open ? this.renderDropdown() : null}
            </div>
        );
    }
}

LanguageDropdown.propTypes = propTypes