import React from 'react';
import PropTypes from "prop-types";
import "styles/keys-page/UnlockPopup.css";
import "styles/ToplButtons.css";
import CloseButton from "../CloseButton";
import axios from "axios";
import {ImSpinner3} from "react-icons/all";

/**
 * Props:
 * Required:
 *  keyfileName: string - name of keyfile that is being unlocked
 *  address: string - address of this keyfile to unlock
 *  toggle: function - to close the pop-up
 *  updateData: function - for updating data (retrieving data from backend)
 *  lockKeyfile: function - for locking keyfile
 *
 *  Optional:
 *  currentUnlocked: string - currently unlocked keyfile
 */
const propTypes = {
    keyfileName: PropTypes.string.isRequired,
    address: PropTypes.string.isRequired,
    toggle: PropTypes.func.isRequired,
    updateData: PropTypes.func.isRequired,
    lockKeyfile: PropTypes.func.isRequired,
    currentUnlocked: PropTypes.string
}

export default class UnlockPopup extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            password: "",
            showWarning: false,
            loading: false,
            errorMsg: ""
        }
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.setState({
            password: event.target.value
        });
    }

    async checkPassword(password) {
        this.setState({loading: true, showWarning: false});
        const unlockNew = {
            jsonrpc: "2.0",
            id: "2",
            method: "wallet_unlockKeyfile",
            params: [{
                "address": this.props.address,
                "password": password
            }]
        }
        const headers = {"x-api-key": "test_key"}
        const response = await axios.post('http://localhost:9086/', unlockNew, {headers});

        if (response.data.result) {
            if (response.data.result[this.props.address] === "unlocked") {
                if (this.props.currentUnlocked) {
                    await this.props.lockKeyfile(this.props.currentUnlocked);
                }
                await this.props.updateData();
                this.setState({loading: false})
                this.props.toggle();
            }
        }else if (response.data.error) {
            this.setState({
                loading: false,
                showWarning: true,
                errorMsg: response.data.error.message,
                password: ""
            });
        }
    }

    render() {
        let render;
        if (this.state.loading) {
            render = <div style={{fontWeight: 400, fontSize: "14px", paddingTop: "15px"}}>
                <ImSpinner3 style={{animation: "rotation 2s infinite linear", marginRight: "5px"}}/>
                Unlocking key file...
            </div>
        }else {
            render = <div>
                <div className={"password-field"}>
                    <span> Password: </span>
                    <input type="password" name={"password"} className={"password-box"} placeholder={"Enter password"} value={this.state.password}
                           onChange={this.handleChange}/>
                </div>
                {this.state.showWarning
                    ? <div style={{margin: "10px", color: "red"}}> {this.state.errorMsg}</div>
                    : null
                }
                <div style={{paddingTop: "15px"}}>
                    <button className={"round-blue-button"} onClick={() => this.checkPassword(this.state.password)}>
                        Unlock
                    </button>
                </div>
            </div>
        }
        return (
            <div className={"unlock-popup"}>
                Unlock {this.props.keyfileName}
                <CloseButton handleClose={this.props.toggle} />
                {render}
            </div>
        );
    }
}

UnlockPopup.propTypes = propTypes