import React from "react";
import PropTypes from "prop-types";
import {NetworkTable} from "./NetworkTable";
import "styles/settings-page/SettingsPage.css";
import "styles/ToplButtons.css";
import {FormAdd} from "grommet-icons";
import Popup from "../Popup";
import EditKeyfileDir from "./EditKeyfileDir";
import LanguageDropdown from "./LanguageDropdown";

/**
 * props (all required):
 *  online: boolean - whether gjallarhorn is connected to bifrost or not.
 *  network: string - name of the current network connected to
 *  chainProvider: string - chainProvider to connect to
 *  connected: function - function to update WalletApp state when connecting to new chainProvider
 *  disconnectFn: function - function to update WalletApp state to offline
 *  currentLanguageCode: string - used for word list
 *  switchLanguage: function - used to switch the current language for word list
 *  keyFileDir: string - the key file directory where the keys are saved
 *  updateData: function - for updating data (Requesting information from backend)
 */
const propTypes = {
    online: PropTypes.bool.isRequired,
    network: PropTypes.string.isRequired,
    chainProvider: PropTypes.string.isRequired,
    connected: PropTypes.func.isRequired,
    disconnectFn: PropTypes.func.isRequired,
    currentLanguageCode: PropTypes.string.isRequired,
    switchLanguage: PropTypes.func.isRequired,
    keyFileDir: PropTypes.string.isRequired,
    updateData: PropTypes.func.isRequired
}

export default class SettingsPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            chainProviders: [{
                name: "Local bifrost",
                chainProvider: this.props.chainProvider,
                editKeyfileDir: false
            }]
        }
        this.editChainProvider = this.editChainProvider.bind(this);
        this.addRow = this.addRow.bind(this);
        this.removeChainProvider = this.removeChainProvider.bind(this);
        this.toggleEditKeyfileDir = this.toggleEditKeyfileDir.bind(this);
    }

    editChainProvider(index, newInfo) {
        let list = this.state.chainProviders;
        list.splice(index, 1, newInfo)
        this.setState({
            chainProviders: list
        });
    }

    removeChainProvider(index) {
        let list = this.state.chainProviders
        list.splice(index, 1)
        this.setState({chainProviders: list})
    }

    addRow() {
        this.setState({
            chainProviders: this.state.chainProviders.concat({
                name: "", chainProvider: ""
            })
        })
    }

    toggleEditKeyfileDir() {
        this.setState({editKeyfileDir: !this.state.editKeyfileDir});
    }

    renderSettings() {
        return (
            <div>
                <ul className={"status"}>
                    <li> <b> Status : </b> {this.props.online ? "Online" : "Offline"}</li>
                    <li> <b> Network : </b> {this.props.network} </li>
                    <li> <b> Chain provider : </b> {this.props.chainProvider} </li>
                    <li> <b> Key file directory : </b> {this.props.keyFileDir} </li>
                    <li> <b> Language : </b> <LanguageDropdown currentLanguageCode={this.props.currentLanguageCode}
                                                               switchLanguage={this.props.switchLanguage}/>
                                                               (Language used for word list)
                    </li>
                </ul>
                {this.props.online ? null : <div style={{color:"red", paddingBottom: "10px"}}>
                    You are currently offline. You can connect to a chain provider below or add a new chain provider.
                </div>}
            </div>);
    }


    render() {
        return (
            <div className={"outer-gray"}>

                {/*Settings*/}
                <div className={"inner-box"} style={{paddingRight: "10px"}}>
                    <div className={"table-title"} style={{paddingBottom: "10px"}}> Settings </div>
                    {this.renderSettings()}

                </div>

                {/*Chain provider table*/}
                <div className={"inner-box"}>
                    <div className={"table-title"}> Chain Providers </div>
                    <NetworkTable chainProviders={this.state.chainProviders} edit={this.editChainProvider}
                                  connected={this.props.connected} currentChainProvider={this.props.chainProvider}
                                  disconnectFn={this.props.disconnectFn} online={this.props.online} remove={this.removeChainProvider}/>
                    <button className={"round-blue-button"} style={{width: "200px", left: "36%", position: "relative", marginTop: "20px"}}
                            onClick={this.addRow}>
                        <FormAdd color={"white"} thickness={"large"} style={{height: "25px", verticalAlign: "middle"}}/>
                        Add new chain provider </button>

                </div>

                {/*Private keys: */}
                <div className={"inner-box"}>
                    <div className={"table-title"}> Private Keys </div>
                    <div> You can find the information for your keys in this file path: </div>
                    <div style={{margin: "10px"}}>
                        <span className={"key-file-directory"}> {this.props.keyFileDir} </span>
                        <button className={"text-button"} onClick={this.toggleEditKeyfileDir}> Edit </button>
                    </div>
                </div>
                {this.state.editKeyfileDir
                    ? <Popup page={<EditKeyfileDir filePath={this.props.keyFileDir} updateData={this.props.updateData}
                                                   toggle={this.toggleEditKeyfileDir}/>}
                             toggle={this.toggleEditKeyfileDir}/>
                    : null}
            </div>
        );
    }
}

SettingsPage.propTypes = propTypes

