import React from "react";
import PropTypes from "prop-types";
import axios from "axios";

/**
 * Props (all required):
 *  filePath: string - the current file path for the key file directory
 *  updateData: function - for updating data after editing keyfile directory
 *  toggle: function - to close pop-up
 */
const propTypes = {
    filePath: PropTypes.string.isRequired,
    updateData: PropTypes.func.isRequired,
    toggle: PropTypes.func.isRequired
}

/**
 * Popup to edit the key file directory (where the private keys are saved)
 */
export default class EditKeyfileDir extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            path: this.editablePath(this.props.filePath)
        }
        this.handleChange = this.handleChange.bind(this);
        this.changeKeyfileDir = this.changeKeyfileDir.bind(this);

    }

    getIndex(filePath) {
        let index = 0
        const root = filePath.indexOf("Bifrost")
        if (root > 0 ){
            index = root + "Bifrost/".length
        }
        return index
    }

    editablePath(filePath) {
        const end = filePath.lastIndexOf("/")
        return filePath.substring(this.getIndex(filePath),end)
    }

    fileRoot(filePath) {
        return filePath.substring(0, this.getIndex(filePath))
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    async changeKeyfileDir() {
        if (this.state.path !== this.editablePath(this.props.filePath)) {
            const request = {
                jsonrpc: "2.0",
                id: "2",
                method: "wallet_changeKeyfileDir",
                params: [{
                    "directory": this.state.path
                }]
            }
            const headers = {"x-api-key": "test_key"}
            await axios.post('http://localhost:9086/', request, {headers});
            await this.props.updateData()
            this.props.toggle();
        }
    }

    render() {
        return (
            <div>
                <div style={{fontSize: "20px", fontWeight: "bold", margin: "15px"}}> Edit current key file directory path: </div>
                <div style={{paddingBottom: "10px"}}>
                    Note: this will switch the key file directory and therefore alter which keyfiles are loaded into your wallet.
                    You can still find your other keys at the old file path.
                </div>
                <div style={{margin: "10px", fontSize: "14px"}}>
                    {this.fileRoot(this.props.filePath)}
                    <input type={"text"} name={"path"} value={this.state.path} onChange={this.handleChange}/>
                </div>

                <button className={"blue-square-button"} onClick={this.changeKeyfileDir}
                        style={{float: "none", marginLeft: "35%", marginTop: "20px"}}>
                    Confirm
                </button>
            </div>
        );
    }
}

EditKeyfileDir.propTypes = propTypes