import {LinkNext} from "grommet-icons";
import React from "react";
import PropTypes from "prop-types";
import axios from "axios";
import TxDataTable from "./TxDataTable";

/**
 * Props (all required):
 *  changeStep: function - for changing the step in the send offline process
 *  signedTx: object - the signed transaction to broadcast (json object)
 *  unsuccessfulTx: function - called when broadcast request returns an error
 *  successfulTx: function - called when broadcast requests is successful
 */
const propTypes = {
    changeStep: PropTypes.func.isRequired,
    signedTx: PropTypes.object.isRequired,
    unsuccessfulTx: PropTypes.func.isRequired,
    successfulTx: PropTypes.func.isRequired
}

//Confirm broadcasting an offline tx
export default class ConfirmSendTx extends React.Component {
    constructor(props) {
        super(props);
        this.broadcastTx = this.broadcastTx.bind(this);
    }

    async broadcastTx() {
        const broadcastRequest = {
            jsonrpc: "2.0",
            id: "2",
            method: "onlineWallet_broadcastTx",
            params: [{
                "method": "topl_broadcastTx",
                "params": [{
                    "tx": this.props.signedTx
                }]
            }]
        }
        const headers = {"x-api-key": "test_key"}
        const response = await axios.post('http://localhost:9086/', broadcastRequest, {headers})
        if (JSON.stringify(response.data.result).includes("Error")) {
            this.props.unsuccessfulTx(response.data.result);
        }else {
            this.props.successfulTx(response.data.result)
        }
    }

    render() {

        return (
            <div style={{paddingTop: "15px", paddingLeft: "15px", fontWeight: "bold"}}>
                Confirm Transaction:
                <TxDataTable rawTx={this.props.signedTx} />
                <button className={"blue-square-button"} onClick={this.broadcastTx}>
                    Send
                    <LinkNext color={"white"} style={{paddingLeft: "10px", verticalAlign: "middle"}}/>
                </button>
            </div>
        )
    }
}

ConfirmSendTx.propTypes = propTypes