import React from "react";
import 'styles/keys-page/KeysPage.css'
import PropTypes from "prop-types";
import KeyCard from "./KeyCard";
import {BsPlusCircle} from "react-icons/all";
import NewKeyfilePopup from "./NewKeyfilePopup";
import {Download} from "grommet-icons";
import ImportKeyfilePopup from "./ImportKeyfilePopup";
import MnemonicPhrasePopup from "./MnemonicPhrasePopup";
import axios from "axios";


/**
 * Props required:
 *  retrievingData: boolean - if still retrieving data = true,
 *  updateData: function - for updating data (requests data from backend)
 *  currentLanguage: string - code for language used for word list
 * Optional:
 *  currentKeyFile: string - publicAddr of current key file
 *  keyFiles: map of public address to name, password, and array of simple assets (boxType, balance),
 *          assetsList: {code, balance}, unlocked: boolean
 */
const propTypes = {
    currentKeyFile: PropTypes.string,
    keyFiles: PropTypes.instanceOf(Map),
    retrievingData: PropTypes.bool.isRequired,
    updateData: PropTypes.func.isRequired,
    currentLanguage: PropTypes.string.isRequired
}

export class KeysPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showNewKeyfilePopup: false,
            showMnemonicPhrasePopup: false,
            showImportKeyfilePopup: false,
            password: "",
            mnemonicPhrase: "",
            seed: ""
        }
        this.toggleNewKeyfilePopup = this.toggleNewKeyfilePopup.bind(this);
        this.toggleImportKeyfilePopup = this.toggleImportKeyfilePopup.bind(this);
        this.toggleMnemonicPhrasePopup = this.toggleMnemonicPhrasePopup.bind(this);
        this.saveMnemonicInfo = this.saveMnemonicInfo.bind(this);
        this.lockKeyfile = this.lockKeyfile.bind(this);
    }

    toggleNewKeyfilePopup() {
        this.setState({showNewKeyfilePopup: !this.state.showNewKeyfilePopup});
    }

    toggleImportKeyfilePopup() {
        this.setState({showImportKeyfilePopup: !this.state.showImportKeyfilePopup});
    }

    toggleMnemonicPhrasePopup() {
        this.setState({showMnemonicPhrasePopup: !this.state.showMnemonicPhrasePopup});
    }

    saveMnemonicInfo(mnemonic, seed, password) {
        this.setState({
            mnemonicPhrase: mnemonic,
            seed: seed,
            password: password
        });
    }

    async lockKeyfile(address) {
        if (address) {
            const lockCurrent = {
                jsonrpc: "2.0",
                id: "2",
                method: "wallet_lockKeyfile",
                params: [{
                    "address": address,
                }]
            }
            const headers = {"x-api-key": "test_key"}
            await axios.post('http://localhost:9086/', lockCurrent, {headers});
            await this.props.updateData();
        }
    }


    render() {
        return(
            <div className={"keys-outer-gray"}>
                <h1>My Keys</h1>
                {this.props.retrievingData
                    ? <div> Loading keyfiles... </div>
                    : <div style={{display: "flex", flexWrap: "wrap"}}>
                        <div className={"add-key-card"}>
                            <button className={"button-style"} onClick={this.toggleNewKeyfilePopup}>
                                <BsPlusCircle className={"plus-sign"} />
                                <h1 style={{verticalAlign: "middle", margin: "0"}}> Add new keyFile </h1>
                            </button>
                            <span className={"or-style"}> OR </span>
                            <button className={"button-style"} onClick={this.toggleImportKeyfilePopup}>
                                <Download color="black" style={{float: "left"}}/>
                                <h1 style={{verticalAlign: "middle", margin: "0"}}> Import keyFile </h1>
                            </button>
                        </div>
                        {Array.from(this.props.keyFiles).map( ([pubAddr, info]) => {
                            return <KeyCard publicAddress={pubAddr} keyFileInfo={info}
                                            currentUnlocked={this.props.currentKeyFile}
                                            updateData={this.props.updateData} lockKeyfile={this.lockKeyfile}/>;
                        })}
                    </div>
                }

                {this.state.showNewKeyfilePopup
                    ? <NewKeyfilePopup toggle={this.toggleNewKeyfilePopup} mnemonicPhraseToggle={this.toggleMnemonicPhrasePopup}
                                        saveMnemonicInfo={this.saveMnemonicInfo} language={this.props.currentLanguage}/>
                    : null}
                {this.state.showMnemonicPhrasePopup
                    ? <MnemonicPhrasePopup toggle={this.toggleMnemonicPhrasePopup} mnemonicPhrase={this.state.mnemonicPhrase}
                                           seed={this.state.seed} password={this.state.password}
                                           lockCurrent={() => this.lockKeyfile(this.props.currentKeyFile)}
                    />
                    : null}
                {this.state.showImportKeyfilePopup
                    ? <ImportKeyfilePopup toggle={this.toggleImportKeyfilePopup} language={this.props.currentLanguage}
                                          lockCurrent={() => this.lockKeyfile(this.props.currentKeyFile)}/>
                    : null}
            </div>
        )
    }
}

KeysPage.propTypes = propTypes