import React from "react";
import "styles/balances-page/send/SendPopup.css";
import "styles/ToplButtons.css";
import PropTypes from "prop-types";
import SendOffline from "./SendOffline";
import CloseButton from "components/CloseButton";
import unselectedSwitch from "images/switch-unselected.svg";
import ConfirmSendPopup from "./online/ConfirmSendPopup";
import ConfirmationPopup from "./online/ConfirmationPopup";
import ReactTooltip from "react-tooltip";
import {BiPlus, BsQuestionCircle} from "react-icons/all";
import SendRecipient from "./SendRecipient";
import axios from "axios";
import UnsuccessfulTx from "./UnsuccessfulTx";
import Popup from "components/Popup";

/**
 * Props required:
 *  boxType: string - the type of box for the asset being sent - "poly", "arbit", or "asset"
 *  currentBalance: number - the balance for this asset
 *  fromAddress: string - public address for currently opened keyfile.
 *  toggleSend: function for toggling send pop-up,
 *  online: boolean - if connected to bifrost = true
 *  showRecentTxMsg: function - to show message that a recent transaction has just occurred
 */
const propTypes = {
    boxType: PropTypes.string.isRequired,
    currentBalance: PropTypes.number.isRequired,
    fromAddress: PropTypes.string.isRequired,
    toggleSend: PropTypes.func.isRequired,
    online: PropTypes.bool.isRequired,
    showRecentTxMsg: PropTypes.func.isRequired
}

/**
 * Steps:
 * Online:
 *      createOnlineTx
 *      confirmTx
 *      showConfirmationPopup
 * Offline process:
 *      createRawTx (online) - create and export raw tx
 *      importRawTxAndSign (offline) - import raw tx, sign, export signed tx
 *      alreadyHaveSignedTx(online) - import signedTx -> confirm tx details + sent -> show confirmation pop-up
 */
export default class SendPopup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sendOffline: false,
            recipients: [
                {
                    address: "",
                    amount: null
                }
            ],
            indexCount: 0,
            confirmTxPopup: false,
            showInputError: false,
            showApiError: false,
            apiErrorMsg: "",
            txParams: [],
            confirmationPopup: false,
            changeAddress: "",
            consolidationAddress: "",
            txId: ""
        }
        this.toggleSendOffline = this.toggleSendOffline.bind(this);
        this.showInputError = this.showInputError.bind(this);
        this.confirmTx = this.confirmTx.bind(this);
        this.postBroadcast = this.postBroadcast.bind(this);
        this.toggleConfirmation = this.toggleConfirmation.bind(this);
        this.addRecipient = this.addRecipient.bind(this);
        this.editRecipient = this.editRecipient.bind(this);
        this.removeRecipient = this.removeRecipient.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.sendTx = this.sendTx.bind(this);
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    toggleSendOffline() {
        this.setState({sendOffline: !this.state.sendOffline});
    }

    capitalize (string) {
        if (typeof string !== 'string') return '';
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    showInputError() {
        this.setState({showInputError: true});
    }

    confirmTx() {
        if (this.state.recipients.filter(recpt => recpt.address === "").length > 0 ||
            this.state.recipients.filter(recpt => recpt.amount <= 0).length > 0 ||
            this.state.recipients.reduce(function (a,b) {return a + b.amount}, 0) > this.props.currentBalance
        ) {
            this.showInputError();
        }else {
            if (!this.state.sendOffline) {
                this.setState({confirmTxPopup: !this.state.confirmTxPopup});
            }
        }
    }

    postBroadcast(txID) {
        this.props.showRecentTxMsg();
        this.setState({txId: txID}, ()=> this.toggleConfirmation());
    }


    toggleConfirmation() {
        if (this.state.confirmationPopup) {
            this.props.toggleSend()
        }
        this.setState({
            confirmTxPopup: false,
            confirmationPopup: !this.state.confirmationPopup
        })

    }
    
    async sendTx() {
        const recipients = this.state.recipients.map(recp => [recp.address, recp.amount])
        let changeAddress = this.props.fromAddress
        if (this.state.changeAddress !== "") {
            changeAddress = this.state.changeAddress
        }
        let txParams = [{
            "method": "topl_raw" + this.capitalize(this.props.boxType) + "Transfer",
            "params": [{
                "propositionType": "PublicKeyCurve25519",
                "sender": [this.props.fromAddress],
                "recipients": recipients,
                "changeAddress": changeAddress,
                "consolidationAddress": this.state.consolidationAddress,
                "fee": 1,
                "data": "",
                "online": true
            }]
        }]
        this.setState({txParams: txParams});
        const data = {
            jsonrpc: "2.0",
            id: "2",
            method: "onlineWallet_createTransaction",
            params: txParams
        }
        const headers = {"x-api-key": "test_key"}
        const response = await axios.post('http://localhost:9086/', data, {headers})
        if (response.data.error) {
            this.setState({
                confirmTxPopup: false,
                showApiError: true,
                apiErrorMsg: response.data.error.message
            })
        } else {
            this.postBroadcast(response.data.result["txId"]);
        }
    }

    addRecipient() {
        this.setState({
            recipients: this.state.recipients.concat({
                address: "",
                amount: null
            })
        });
    }

    editRecipient(recipientInfo, index) {
        let newRecipients = this.state.recipients;
        newRecipients.splice(index, 1, recipientInfo);
        this.setState({recipients: newRecipients});
    }
    
    removeRecipient(index) {
        let newRecipients = this.state.recipients;
        newRecipients.splice(index, 1);
        this.setState({recipients: newRecipients});
    }


    renderChangeAddress() {
        let marginBelow = "0"
        if (this.props.boxType === "poly") {marginBelow = "20px"}
        return (<div className={"field"} style={{marginTop: "0px", marginBottom: marginBelow}}>
                                    <span> Change address:
                                        <BsQuestionCircle data-tip="" data-for={"change-address"} style={{verticalAlign: "middle", marginLeft: "5px"}}/>
                                    </span>
            <input type="text" name={"changeAddress"} className={"address-box"}
                   placeholder={this.props.fromAddress} value={this.state.changeAddress}
                   style={{height: "20px"}} onChange={this.handleChange}/>
            <ReactTooltip id={"change-address"} place={"bottom"} effect={"solid"}>
                <span style={{fontWeight: "500"}}> Address to send leftover Poly's to </span>
            </ReactTooltip>
        </div>)
    }

    renderConsolidationAddress() {
        return (<div className={"field"} style={{marginTop: "0px", marginBottom: "20px"}}>
            <div style={{float: "left"}}>
                <div> Consolidation address: </div>
                <div>
                    (optional)
                    <BsQuestionCircle data-tip="" data-for={"consolidation-address"}
                                      style={{verticalAlign: "middle", marginLeft: "5px"}}/>
                </div>
            </div>
            <input type="text" name={"consolidationAddress"} className={"address-box"}
                   placeholder={"Enter address"} value={this.state.consolidationAddress}
                   style={{height: "20px"}} onChange={this.handleChange}/>
            <ReactTooltip id={"consolidation-address"} place={"bottom"} effect={"solid"}>
                <span style={{fontWeight: "500"}}> Address to send leftover {this.props.boxType}'s to </span>
            </ReactTooltip>
        </div>)
    }

    renderPopup() {
        let currency;
        if (this.props.boxType === "poly") {
            currency = "POLY"
        }else if (this.props.boxType === "arbit") {
            currency = "ARB"
        }

        if (this.state.confirmTxPopup) {
            //confirm sending online tx
            return <ConfirmSendPopup toggle={this.confirmTx} fromAddress={this.props.fromAddress}
                                     recipients={this.state.recipients} boxType={currency}
                                     sendTx={this.sendTx}/>
        }
        else if (this.state.confirmationPopup) {
            //confirmation message after sending online tx
            return <ConfirmationPopup  toggle={this.toggleConfirmation}
                                       txId={this.state.txId}/>
        } else if (this.state.showApiError) {
            return <Popup page={<UnsuccessfulTx signedTx={JSON.stringify(this.state.txParams)} errorMsg={this.state.apiErrorMsg}/>}
                          toggle={()=> this.setState({showApiError: false})}/>
        }
    }

    render() {
        let errorMsg = "";
        if (this.state.showInputError) {
            if (this.state.recipients.filter(recpt => recpt.amount <= 0).length > 0) {
                errorMsg += " an amount box is less than zero: " + this.state.recipients.filter(recpt => recpt.amount <= 0);
            }
            if (this.state.recipients.filter(recpt => recpt.amount))
            if (this.state.recipients.reduce(function (a,b) {return a + b.amount}, 0) > this.props.currentBalance) {
                errorMsg += " not enough balance for the transaction: " + this.state.recipients.reduce(function (a, b) {
                    return a + b.amount
                }, 0);
            }
        }

        return (
            <div>
            {this.state.confirmationPopup
                ? this.renderPopup()
                : <div className={"send-tab"}>
                    <div><CloseButton handleClose={this.props.toggleSend}/></div>

                    {(this.state.sendOffline || !this.props.online)
                        ? <SendOffline toggleSendOffline={this.toggleSendOffline} fromAddress={this.props.fromAddress}
                                       recipients={this.state.recipients} postBroadcast={this.postBroadcast} online={this.props.online}
                                       changeAddress={this.state.changeAddress} consolidationAddress={this.state.consolidationAddress}
                                       boxType={this.props.boxType} toggleSend={this.props.toggleSend} showError={this.showInputError}/>
                        : (<div>
                            <div style={{marginTop: "30px", marginBottom: "20px"}}>
                                {this.state.recipients.map((recipient, index) => {
                                    return <SendRecipient boxType={this.props.boxType} showRemoveButton={this.state.recipients.length > 1}
                                                          removeFunc={() => this.removeRecipient(index)}
                                                          saveNewInfo={(newInfo) => this.editRecipient(newInfo, index)}
                                                          error={this.state.showInputError}/>
                                })}
                            </div>

                            <button className={"text-button"} style={{paddingLeft: "13px", paddingBottom: "20px"}} onClick={this.addRecipient}>
                                <BiPlus className={"plus-symbol"}/>
                                Add Recipient
                            </button>
                            {this.renderChangeAddress()}
                            {this.props.boxType !== "poly"
                                ? this.renderConsolidationAddress() : null}
                            <span className={"field"} style={{paddingTop: "10px"}}>
                                Send offline
                                <img src={unselectedSwitch} className="offline-switch" alt="offline-switch" onClick={this.toggleSendOffline}/>
                            </span>
                            <button onClick={this.confirmTx} className={"blue-square-button"}>
                                Next
                            </button>
                            {this.renderPopup()}
                            {errorMsg}
                        </div>)
                    } </div> }
            </div>
        )
    }
}

SendPopup.propTypes = propTypes