import React from "react";
import QRCode from "qrcode.react";
import PropTypes from "prop-types";
import CopyButton from "../CopyButton";
import "styles/keys-page/KeyDetails.css";

/**
 * Props (all required):
 *  address: string - address for current key file
 *  name: string - name of keyfile
 *  unlocked: boolean - if this key is unlocked
 *  lockKeyfile: function - for locking this keyfile
 */
const propTypes = {
    address: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    unlocked: PropTypes.bool.isRequired,
    lockKeyfile: PropTypes.func.isRequired
}

export default class KeyDetails extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            editMode: false,
            keyfileName: this.props.name
        }
    }

    render() {
        return (
            <div style={{color: "black", boxShadow: "none"}}>
                <h1> {this.state.keyfileName}</h1>
                <p style={{float: "left", fontSize: "15px", marginTop: "10px"}} id={"details-address"}> {this.props.address} </p>
                <div style={{float: "left"}}> <CopyButton elementIDToCopy={"details-address"} /> </div>
                <div style={{marginLeft: "40%"}}><QRCode value={this.props.address}/></div>
                {this.props.unlocked
                    ? <button className={"round-blue-button"} style={{position: "relative", left: "38%", marginTop: "20px", width: "150px",
                        fontSize: "18px", height: "40px", borderRadius: "30px"}} onClick={() => this.props.lockKeyfile(this.props.address)}>
                        Lock
                    </button>
                    : null}
            </div>
        )
    }
}

KeyDetails.propTypes = propTypes