import React from "react";
import PropTypes from "prop-types";
import CloseButton from "components/CloseButton";
import "styles/balances-page/assets/CreateAssetPopup.css";
import "styles/ToplButtons.css";
import axios from "axios";

/**
 * Props (all required):
 *  currentAddress: string
 *  toggle - function to close the pop-up
 *  showRecentTxMsg - function to show message about recent tx
*/
const propTypes = {
    currentAddress: PropTypes.string.isRequired,
    toggle: PropTypes.func.isRequired,
    showRecentTxMsg: PropTypes.func.isRequired
}

export default class CreateAssetPopup extends React.Component {

    /**
     * assetName: string
     * recipient: string - address of transfer recipient
     * amount: number - amount of asset to send
     * fee: number (optional)
     * data: string (optional) data string associated with this tx.
     */
    constructor(props) {
        super(props);
        this.state = {
            assetName: "",
            recipient: "",
            amount: 0,
            fee: 0,
            data: "",
            showWarning: false,
            errorMsg: null
        }
        this.handleChange = this.handleChange.bind(this);
        this.createNewAsset = this.createNewAsset.bind(this);
    }

    handleChange(event) {
        if (event.target.name === "amount") {
            this.setState({amount: parseInt(event.target.value)})
        }else {
            this.setState({
                [event.target.name]: event.target.value});
        }
    }

    async createNewAsset() {
        if (this.state.assetName === "" || this.state.recipient === "" || this.state.amount <= 0 || !this.state.amount
            || this.state.assetName.length > 8) {
            this.setState({showWarning: true});
        }else{
            const createAssetRequest = {
                jsonrpc: "2.0",
                id: "2",
                method: "onlineWallet_createTransaction",
                params: [{
                    "method": "topl_rawAssetTransfer",
                    "params": [{
                        "propositionType": "PublicKeyCurve25519",
                        "sender": [this.props.currentAddress],
                        "recipients": [this.state.recipient],
                        "amount": this.state.amount,
                        "issuer": this.props.currentAddress,
                        "shortName": this.state.assetName,
                        "changeAddress": this.props.currentAddress,
                        "minting": true,
                        "fee": 1,
                        "online": true
                    }]
                }]
            }
            const headers = {"x-api-key": "test_key"}
            let response = await axios.post('http://localhost:9086/', createAssetRequest, {headers})
            if (response.data.error) {
                this.setState({errorMsg: response.data.error.message});
            }else {
                this.props.showRecentTxMsg();
                this.props.toggle()
            }
        }
    }


    //Todo: add fee and data fields?
    render() {
        let nameBox = "input-box";
        let recipientBox = "input-box";
        let amountBox = "input-box";
        if (this.state.showWarning) {
            if (this.state.recipient === "") {recipientBox = "input-warning-box"}
            if (this.state.assetName === "" || this.state.assetName.length > 8) { nameBox = "input-warning-box"}
            if (this.state.amount <= 0 || !this.state.amount) { amountBox = "input-warning-box"}
        }
        return (
            <div className={"asset-popup"}>
                Create New Asset
                <CloseButton handleClose={this.props.toggle}/>
                <div className={"input-field"}>
                    <span> Asset name: </span>
                    <input className={nameBox} type={"text"} name={"assetName"} value={this.state.assetName}
                           onChange={this.handleChange}/>
                    {(this.state.showWarning && this.state.assetName.length > 8)
                        ? <span className={"missing-fields"}> The asset name must be 8 characters or less. </span> : null}
                </div>
                <div className={"input-field"}>
                    <span> Recipient: </span>
                    <input className={recipientBox} type={"text"} name={"recipient"} style={{width: "400px"}}
                           value={this.state.recipient} onChange={this.handleChange}/>
                </div>
                <div className={"input-field"}>
                    <span> Amount: </span>
                    <input className={amountBox} type={"number"} name={"amount"} value={this.state.amount}
                           onChange={this.handleChange}/>
                </div>
                {(this.state.showWarning && (this.state.assetName === "" || this.state.recipient === "" || this.state.amount <= 0 || !this.state.amount))
                    ? <span className={"missing-fields"}> Please fill in the missing field(s) above.</span>
                    : null}
                {this.state.errorMsg ? <div style={{color: "red"}}> Error: {this.state.errorMsg} </div> : null}
                <button className={"round-blue-button"} style={{float: "right", marginTop: "20px"}}
                        onClick={this.createNewAsset}>
                    Submit
                </button>
            </div>
        );
    }
}

CreateAssetPopup.propTypes = propTypes