import React from "react";
import PropTypes from "prop-types";
import "styles/full-page/TopBar.css"
import {Menu} from "grommet-icons/es6";
import {NetworkDropdown} from "./NetworkDropdown";
import unselectedSwitch from "images/switch-unselected.svg";
import selectedSwitch from "images/switch-selected.svg";
import {MdWifi, RiWifiOffFill} from "react-icons/all";

/**
 * Props (all required):
 *  toggleMenu: function - function to toggle side menu
 *  networkType: string - the currently selected network
 *  toggleConnection: function - used to connect or disconnect to bifrost
 *  online: boolean - if currently connected to bifrost
 *  changeNetwork: function - to switch networks
 *  showError: function - to show error message
 */
const propTypes = {
    toggleMenu: PropTypes.func.isRequired,
    networkType: PropTypes.string.isRequired,
    toggleConnection: PropTypes.func.isRequired,
    online: PropTypes.bool.isRequired,
    changeNetwork: PropTypes.func.isRequired,
    showError: PropTypes.func.isRequired
}

export default class TopBar extends React.Component {

    render() {
        return(
            <div className={"top-bar"}>
                <button className={"side-menu-button"} onClick={this.props.toggleMenu} >
                <Menu style={{height: "20px", paddingLeft: "24px", paddingTop: "15px"}}/>
                </button>
                <div className={"top-right-icons"}>
                    {this.props.online
                        ? <span>
                            <MdWifi style={{height: "30px", width: "30px", verticalAlign: "middle"}} />
                            <span style={{verticalAlign: "middle"}}> Connected </span>
                            <img src={selectedSwitch} className="offline-switch" style={{paddingRight: "20px", width: "35px", marginLeft: "5px"}}
                                 alt={"online-switch"} onClick={this.props.toggleConnection}/>
                        </span>
                        : <span>
                            <RiWifiOffFill style={{height: "27px", width: "27px", verticalAlign: "middle"}}/>
                            <span style={{verticalAlign: "middle"}}> Disconnected </span>
                            <img src={unselectedSwitch} className="offline-switch" style={{paddingRight: "20px", width: "35px", marginLeft: "5px"}}
                                 alt={"offline-switch"} onClick={this.props.toggleConnection}/>
                        </span>
                    }
                    <NetworkDropdown currentNetwork={this.props.networkType} online={this.props.online}
                                     changeNetwork={this.props.changeNetwork} showError={this.props.showError}/>
                </div>
            </div>
        );
    }
}

TopBar.propTypes = propTypes